import { collectionSchemaBrand, } from './schema.js';
/**
 * An universal query definition
 */
export class Query {
    base;
    constraints;
    constructor(base, constraints) {
        this.base = base;
        this.constraints = constraints;
    }
}
/**
 * Builds a new query
 */
export const query = (base, ...constraints) => {
    if (base instanceof Query) {
        // extends another query
        return new Query({ kind: 'extends', query: base }, constraints);
    }
    if (collectionSchemaBrand in base) {
        // root collection
        return new Query({ kind: 'collection', collection: base, parentId: {} }, constraints);
    }
    // subcollection
    return new Query({ kind: 'collection', collection: base.collection, parentId: base.parent }, constraints);
};
/**
 * Builds a new collection group query
 */
export const collectionGroupQuery = (collection, ...constraints) => {
    return new Query({ kind: 'collectionGroup', collection }, constraints);
};
export const orderBy = (field, direction) => ({ kind: 'orderBy', field, direction });
export const limit = (limit) => ({ kind: 'limit', limit });
export const limitToLast = (limit) => ({
    kind: 'limitToLast',
    limit,
});
export const startAt = (...cursor) => ({
    kind: 'startAt',
    cursor,
});
export const startAfter = (...cursor) => ({
    kind: 'startAfter',
    cursor,
});
export const endAt = (...cursor) => ({
    kind: 'endAt',
    cursor,
});
export const endBefore = (...cursor) => ({
    kind: 'endBefore',
    cursor,
});
/**
 * Returns a single filter condition
 */
export const condition = (fieldPath, opStr, value) => ({ kind: 'where', fieldPath, opStr, value });
export const or = (...filters) => ({
    kind: 'or',
    filters,
});
export const and = (...filters) => ({
    kind: 'and',
    filters,
});
